}

.section-enterprise-2 {
  background-color: var(--dark);
  background-image: linear-gradient(#0c303633, #0c303633), url("../images/gradient-top.png");
  background-position: 0 0, 50% 0;
  background-repeat: repeat, no-repeat;
  background-size: auto, auto;
  border-top-left-radius: 40px;
  border-top-right-radius: 40px;
  margin-top: -40px;
  padding-top: 100px;
  padding-bottom: 100px;
}

.strip-box {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.strip-left {
  width: 45%;
}

.strip-right {
  text-align: right;
  width: 45%;
  display: flex;
}

.strip-right-img-lightbox {
  background-color: #5bd6b8;
  border-radius: 12px;
  width: 100%;
  max-width: 420px;
  max-height: 300px;
  margin-left: auto;
  margin-right: 0;
  padding: 24px;
  transition: all 0.3s;
  display: block;
  position: relative;
}
.strip-right-img-lightbox .strip-right-img {
  vertical-align: middle;
  max-width: 100%;
  display: inline-block;
}

.strip-right-img-lightbox:hover {
  background-color: #47c2a4;
}

.strip-play-btn {
  background-image: url("../images/icon-play.svg"), linear-gradient(to bottom, var(--dark-green), var(--dark-green));
  background-position: 50%, 0 0;
  background-repeat: no-repeat, repeat;
  background-size: auto, auto;
  border-radius: 100px;
  width: 100px;
  height: 100px;
  margin-top: -60px;
  margin-left: -50px;
  position: absolute;
  inset: 50% auto auto 50%;
}

.mobile-navigation {
  display: none;
}

.mobile-navigation .menu-toggle {
  display: none;
}

.mobile-navigation .menu-icon {
  display: block;
  cursor: pointer;
  font-size: 24px;
}

.mobile-navigation .menu-items {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: #fff;
  padding: 30px 20px 60px;
}

.mobile-navigation .menu-toggle:checked ~ .menu-items {
  display: flex;
  flex-direction: column;
}

.mobile-navigation .navlink-btn-border {
  margin: 0;
  margin-top: 10px;
}

.mobile-navigation .menu-icon {
  background-image: url("https://cdn.prod.website-files.com/665ab0daac869acad030a349/665abeedcf1a6fb4bd4c6f85_menu-open.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 24px;
  width: 40px;
  height: 40px;
}

.mobile-navigation .menu-toggle:checked ~ .menu-icon {
  background-color: var(--transparent);
  background-image: url("https://cdn.prod.website-files.com/665ab0daac869acad030a349/665abeed22404117b377d6f5_menu-close.svg");
  background-size: 20px;
}

.mobile-navigation .menu-items {
  display: none;
  animation: fade 0.2s ease-out;
  z-index: 10;
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

